a, .faux-a, .mce-container a {
  color: $text-main;
  
    &.btn {
      font-family: $titleFont;
      font-weight: 700;
      text-decoration: none;
      display: inline-block;
      position: relative;
      appearance: none;
      color: $button-text;
      line-height: 1;
      z-index: 1;
      transition: all $trans2 ease;
      
        &::before {
          content: " ";
          display: block;
          width: 100%;
          height: 100%;
          background: $fishTint1;
          position: absolute;
          top: 50%;
          left: 50%;
          transform: translate(-50%, -50%) skewX(-8deg);
          z-index: -1;
          transition: all $trans2 ease;
        }

        @media (hover: hover) and (pointer: fine) {
          &:hover {
            color: #fff;
            text-decoration: none;

            &::before {
              background: $button-primary-hover;
              transform: translate(-50%, -50%) skewX(0deg);
            }
          }
        }

        &.btn-lg {
          padding: 14px 24px;
          font-size: $b1;
        }

        &.btn-md {
          padding: 12px 26px;
          font-size: $b3;
        }

        &.btn-sm {
          padding: 10px 26px;
          font-size: $b5;
        }

    }
    
    &.link {
      font-weight: 700;
      text-decoration: none;
      display: inline-block;
      position: relative;
      color: $text-main;
      
        &::after {
          content: " ";
          display: inline-block;
          width: 0;
          height: 0;
          border-style: solid;
          border-width: 6px 0 6px 12px;
          border-color: transparent transparent transparent $fishTint1;
          transform: translateX(6px);
          position: relative;
          vertical-align: middle;
          transition: all $trans2 ease;
        }
        
        @media (hover: hover) and (pointer: fine) {
          &:hover {
            text-decoration: none;
            
              &::after {
                border-color: transparent transparent transparent $fishShade1;
                transform: translateX(10px);
              }
            
          }
        }
      
        &.link-lg {
          font-size: $b1;
        }
        
        &.link-md {
          font-size: $b2;
        }
        
        &.link-sm {
          font-size: $b3;
        }
      
    }
    
    &.plain {
      text-decoration: none;
      color: inherit;
    }
  
}

.wp-block-button__link, .c-button--primary {
  font-family: $titleFont;
  font-weight: 700;
  padding: 14px 24px;
  font-size: $b1;
  text-decoration: none;
  display: inline-block;
  position: relative;
  appearance: none;
  color: $button-text;
  line-height: 1;
  z-index: 1;
  transition: all $trans2 ease;
  
    &::before {
      content: " ";
      display: block;
      width: 100%;
      height: 100%;
      background: $fishTint1;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%) skewX(-8deg);
      z-index: -1;
      transition: all $trans2 ease;
    }

    @media (hover: hover) and (pointer: fine) {
      &:hover {
        color: #fff;
        text-decoration: none;

        &::before {
          background: $button-primary-hover;
          transform: translate(-50%, -50%) skewX(0deg);
        }
      }
    }
}

.button-wrap {
  margin-top: $m40;
}

.post-copy, .blog-copy {
  
  a:not(.btn):not(.link):not(.wp-block-button__link):not(.c-button--primary) {
    transition: all $trans2 ease;
    
      @media (hover: hover) and (pointer: fine) {
        &:hover {
          color: $fishTint1;
        }
      }
    
  }
  
}